<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
component(
  v-bind="$props"
  @close="onClose"
  :class=`[
    "c-twitter-tweet",
    "c-twitter-tweet--" + layout,
    "c-twitter-tweet--" + size,
    "c-twitter-tweet--" + theme,
    {
      "c-twitter-tweet--loading": loading
    }
  ]`
  :is="layout + '-layout'"
  :is-closed="closed"
  :is-triggered="triggered"
  :transition="transition"
  direction="vertical"
  popover-size="large"
)
  widget-header(
    :size="size"
    :theme="theme"
    slot="header"
  )
    .c-twitter-tweet__header
      img(
        v-if="twitterAvatar"
        @click="onGoToTwitter"
        :src="twitterAvatar"
        class="c-twitter-tweet__avatar"
      )

      twitter-logo(
        v-else
        @click="onGoToTwitter"
        class="c-twitter-tweet__avatar"
      )

      .c-twitter-tweet__information
        span(
          @click="onGoToTwitter"
          class="c-twitter-tweet__name"
        ) {{ twitterName || twitterUsername }}

        button(
          @click="onGoToTwitter"
          class="c-twitter-tweet__button"
        ) {{ buttonContent }}

  div(
    v-if="twitterTweetUrl"
    class="c-twitter-tweet__body"
    slot="body"
  )
    base-spinner(
      v-if="loading"
      :size="size"
      class="c-twitter-tweet__spinner"
    )

    div(
      v-show="!loading"
      v-html="oEmbedHtml"
      class="c-twitter-tweet__tweet"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import debounce from "lodash/debounce"
import jsonp from "jsonp"
import { BaseSpinner } from "@growthbunker/vuedarkmode"

// PROJECT: COMPONENTS
import TwitterLogo from "@/components/logos/TwitterLogo"
import WidgetHeader from "@/components/common/WidgetHeader"

// PROJECT: HELPERS
import { loadScript } from "@/helpers/widgets"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/main/CloseMixin"
import EditorMixin from "@/mixins/main/EditorMixin"
import LayoutMixin from "@/mixins/main/LayoutMixin"
import PositionMixin from "@/mixins/main/PositionMixin"
import SizeMixin from "@/mixins/main/SizeMixin"
import SubscriptionMixin from "@/mixins/main/SubscriptionMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"
import TransitionMixin from "@/mixins/main/TransitionMixin"
import TriggerMixin from "@/mixins/main/TriggerMixin"
import WidgetMixin from "@/mixins/main/WidgetMixin"

export default {
  name: "TwitterTweet",

  components: {
    BaseSpinner,
    TwitterLogo,
    WidgetHeader
  },

  mixins: [
    CloseMixin,
    EditorMixin,
    LayoutMixin,
    PositionMixin,
    SizeMixin,
    ThemeMixin,
    SubscriptionMixin,
    TransitionMixin,
    TriggerMixin,
    WidgetMixin
  ],

  props: {
    buttonContent: {
      type: String,
      default: null
    },
    twitterAvatar: {
      type: String,
      default: null
    },
    twitterName: {
      type: String,
      default: null
    },
    twitterTweetUrl: {
      type: String,
      default: null
    },
    twitterUsername: {
      type: String,
      required: true
    }
  },

  data: () => ({
    // --> STATE <--

    loading: false,
    oEmbedHtml: null
  }),

  watch: {
    layout: {
      handler() {
        this.loadTweet()
      }
    },
    twitterTweetUrl: {
      handler: debounce(function() {
        this.loadTweet()
      }, 500)
    }
  },

  mounted() {
    this.loadTweet()
  },

  methods: {
    // --> HELPERS <--

    async loadTweet() {
      if (this.twitterTweetUrl) {
        const endpoint = `https://publish.twitter.com/oembed?url=${this.twitterTweetUrl}`

        this.loading = true

        if (!window.twttr) {
          await loadScript("https://platform.twitter.com/widgets.js")
        }

        jsonp(endpoint, null, (error, data) => {
          if (error) {
            console.error(error)

            this.loading = false
          } else {
            this.oEmbedHtml = data.html

            this.$nextTick(() => {
              try {
                window.twttr.widgets.load()
              } catch (error_) {
                console.warn(error_)
              }
            })
          }
        })

        // Hide spinner when widget is loaded
        window.twttr.events.bind("rendered", () => {
          this.loading = false
        })
      }
    },

    // --> EVENT LISTENERS <--

    onGoToTwitter() {
      window.open(`https://www.twitter.com/${this.twitterUsername}`, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-twitter-tweet";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  #{$c}__header {
    display: flex;
    margin: 0 auto;
    max-width: 500px;

    #{$c}__avatar {
      flex: 0 0 auto;
      margin-top: 2px;
      border-radius: 100%;
      cursor: pointer;
    }

    #{$c}__information {
      display: flex;
      align-items: flex-start;
      flex: 1;
      flex-direction: column;

      #{$c}__name {
        font-weight: 800;
        cursor: pointer;

        &:hover {
          text-decoration: underline;
        }
      }

      #{$c}__button {
        position: relative;
        display: inline-block;
        overflow: hidden;
        min-width: 60px;
        outline: none;
        border: 0;
        border: 1px solid #1da1f2;
        border-radius: 25px;
        background-color: transparent;
        color: #1da1f2;
        vertical-align: middle;
        text-align: center;
        white-space: nowrap;
        font-weight: 800;
        transition: all 125ms linear;
        user-select: none;
        cursor: pointer;

        @include no-tap-highlight-color;

        &:hover {
          background-color: rgba(#1da1f2, 0.1);
        }
      }
    }
  }

  #{$c}__body {
    #{$c}__tweet {
      display: flex;
      justify-content: center;

      .twitter-tweet {
        flex: 0 0 auto !important;
        margin: 0 !important;
        width: 100% !important;
      }
    }
  }

  // --> LAYOUTS <--

  &--banner {
    #{$c}__body {
      #{$c}__tweet {
        .twitter-tweet {
          width: auto !important;
        }
      }
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      #{$c}__header {
        #{$c}__avatar {
          margin-right: 10px + (1px * $i);
          width: 40px + (5px * $i);
          height: 40px + (5px * $i);
        }

        #{$c}__information {
          #{$c}__name {
            margin-bottom: 6px + (1px * $i);
            font-size: 12px + (1px * $i);
            line-height: 12px + (1px * $i);
          }

          #{$c}__button {
            padding: (7px + (1px * $i)) (10px + (2px * $i));
            font-size: 10px + (1px * $i);
          }
        }
      }

      #{$c}__body {
        #{$c}__tweet {
          padding: 8px + (1px * $i);
        }
      }

      &#{$c}--loading {
        #{$c}__body {
          padding: 40px + (5px * $i) 0;
        }
      }
    }
  }

  // --> BOOLEANS <--

  &--loading {
    #{$c}__body {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__header {
        #{$c}__avatar {
          box-shadow: 0 1px 3px mdg($theme, "shadows", "default", "primary");
        }
      }
    }
  }
}
</style>
