/**************************************************************************
 * HELPERS > WIDGETS
 ***************************************************************************/

/**
 * Load an external component
 * @param {String} slug
 * @return {Object} widget
 */
export const loadWidget = function(slug) {
  // Do not load widget when already loaded
  if (window[slug]) {
    return window[slug]
  }

  return new Promise((resolve, reject) => {
    const script = document.createElement("script")

    script.async = true
    script.src = `${process.env.VUE_APP_WIDGETS_URL}/${slug}/${slug}.umd.min.js`

    // Bind event listeners
    script.addEventListener("load", () => {
      resolve(window[slug])
    })
    script.addEventListener("error", () => {
      reject(new Error(`Error loading ${slug}`))
    })

    document.head.append(script)
  })
}

/**
 * Load an external script if it does not exist
 * @param {String} src
 * @param {Boolean} False
 * @return {Promise}
 */
export const loadScript = function(src, force = false) {
  return new Promise(function(resolve, reject) {
    // Check if a script already exists
    const existingScript = document.querySelector(`script[src="${src}"]`)

    if (existingScript && !force) {
      if (existingScript.classList.contains("is-loading")) {
        existingScript.addEventListener("load", resolve)
        existingScript.addEventListener("error", reject)
        existingScript.addEventListener("abort", reject)
      } else {
        resolve()
      }

      return
    }

    // Create a new script append it to the head of the document
    const script = document.createElement("script")

    script.type = "text/javascript"
    script.async = true
    script.src = src
    script.classList.add("is-loading")

    script.addEventListener("load", () => {
      script.classList.remove("is-loading")

      resolve()
    })

    script.addEventListener("error", reject)
    script.addEventListener("abort", reject)

    document.head.append(script)
  })
}
